import React from 'react';
import { motion } from 'framer-motion';

interface BurgerProps {
  openable: boolean;
}

const variants = {
  openable: {
    d: 'M 0,7 L 20,7 M 0,13 L 20,13',
  },
  closeable: {
    d: 'M 3,3 L 17,17 M 3,17 L 17,3',
  },
};

const Burger = ({ openable }: BurgerProps) => (
  <motion.svg
    className="w-6 h-6"
    aria-hidden="true"
    stroke="currentColor"
    viewBox="0 0 20 20"
    xmlns="http://www.w3.org/2000/svg"
  >
    <motion.path
      variants={variants}
      animate={openable ? 'openable' : 'closeable'}
      transition={{ duration: 0.25, type: 'spring' }}
    />
  </motion.svg>
);

export { Burger };
