/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useCallback, useState } from 'react';
import { Header } from 'components/atoms';
import { motion } from 'framer-motion';
import { BurgerButton } from '../burger-button/burger-button';

interface MenuTopProps {
  logo?: React.ReactNode;
  children?: React.ReactNode | React.ReactNode[];
}

const MenuTop = ({ logo, children }: MenuTopProps) => {
  const [isVisible, setIsVisible] = useState(false);
  const toggleVisible = useCallback(() => {
    setIsVisible((visible) => !visible);
  }, []);

  return (
    <Header>
      <a href="/" className="flex items-center">
        {logo}
      </a>
      <div className="flex md:order-2">
        <BurgerButton onClick={toggleVisible} isVisible={isVisible} />
      </div>
      <div
        className="justify-between items-center w-full md:flex md:w-auto md:order-1"
        id="navbar-sticky"
      >
        <motion.ul
          className={`
            transition-all translate-x-[${
              isVisible ? 0 : 100
            }%] mt-2.5 right-0 flex flex-col justify-center max-w-screen-md w-screen p-4 h-screen bg-black content-start [&>*]:h-min
            md:flex-row md:space-x-8 md:mt-0 md:font-medium md:h-full md:translate-x-[unset]
            `}
        >
          {children}
        </motion.ul>
      </div>
    </Header>
  );
};

export { MenuTop };
