/* eslint-disable react/jsx-props-no-spreading */
import React from 'react';
import { Burger } from '../burger/burger';

type BurgerButtonProps = React.HTMLAttributes<HTMLButtonElement> & {
  isVisible: boolean;
};

const BurgerButton = (props: BurgerButtonProps) => {
  const { isVisible, ...buttonProps } = props;
  return (
    <button
      data-collapse-toggle="navbar-sticky"
      type="button"
      className="inline-flex items-center p-2 text-sm text-gray-500 rounded-lg md:hidden focus:outline-none focus:ring-2 dark:text-gray-400 dark:hover:bg-gray-700 dark:focus:ring-gray-600"
      aria-controls="navbar-sticky"
      aria-expanded="false"
      {...buttonProps}
    >
      <Burger openable={!isVisible} />
    </button>
  );
};

export { BurgerButton };
