/* eslint-disable no-undef */
import React, { useState } from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import { GatsbyImage, getImage, IGatsbyImageData } from 'gatsby-plugin-image';
import { AnimatePresence, motion } from 'framer-motion';

const SectionHome = () => {
  const data = useStaticQuery(graphql`
query MyQuery {
  allImageSharp {
    edges {
      node {
        parent {
          ... on File {
            relativeDirectory
          }
        }
        gatsbyImageData(
          placeholder: BLURRED
          formats: [AUTO, WEBP, AVIF]
        )
      }
    }
  }
}
  `);

  const images: IGatsbyImageData[] = data.allImageSharp.edges
    .filter((node) => node.node.parent.relativeDirectory === 'home-slides')
    .map((node) => getImage(node.node.gatsbyImageData));
  const [currentImageIndex, setCurrentImageIndex] = useState(0);

  // useEffect(() => {
  //   const interval = setInterval(() => {
  //     setCurrentImageIndex((imageIndex) => (
  //       imageIndex === images.length - 1 ? 0 : imageIndex + 1
  //     ));
  //   }, 3000);
  //   return () => clearInterval(interval);
  // }, []);

  return (
    <section className="relative h-full flex flex-col justify-center items-center text-white">
      <motion.h1
        className="text-7xl font-medium uppercase"
        initial={{
          translateY: '100px',
          opacity: 0,
        }}
        animate={{
          scale: 1,
          opacity: 1,
          translateY: 0,
          transition: {
            duration: 0.5,
            type: 'spring',
          },
        }}
      >
        Title
      </motion.h1>
      <motion.h2
        className="text-xl font-light"
        initial={{
          translateY: '100px',
          opacity: 0,
        }}
        animate={{
          scale: 1,
          opacity: 1,
          translateY: 0,
          transition: {
            delay: 0.25,
            duration: 0.5,
            type: 'spring',
          },
        }}
      >
        Subtitle
      </motion.h2>
      <div className="absolute top-0 left-0 z-[-1] h-full w-full bg-black">
        <AnimatePresence mode="wait">
          <motion.div
            className="h-full w-full"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            transition={{ duration: 2 }}
            key={currentImageIndex}
            onAnimationComplete={() => setTimeout(() => setCurrentImageIndex((imageIndex) => (
              imageIndex === images.length - 1 ? 0 : imageIndex + 1
            )), 4000)}
          >
            <GatsbyImage
              className="h-full w-full brightness-50"
              image={images[currentImageIndex]}
              alt="test"
            />
          </motion.div>
        </AnimatePresence>
      </div>
    </section>
  );
};

export { SectionHome };
